.slide {
  position: relative;
  margin: 0;
  width: 100%;
  height: 356px;
  background: #ccc;
  &.slide-small {
    height: 162px;
  }
  ul li {
    position: absolute;
    left: 0;
    top: 0;
    display: block;
    width: 100%;
    height: 100%;
    list-style: none;
  }
  .dots {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 20px;
    width: 100%;
    z-index: 3;
    text-align: center;
    li {
      display: inline-block;
      margin: 0 10px;
      width: 10px;
      height: 10px;
      border: 2px solid #fff;
      border-radius: 50%;
      opacity: 0.4;
      cursor: pointer;
      transition: background .5s, opacity .5s;
      list-style: none;
      &.active, &.active-big {
        background: #fff;
        opacity: 1;
      }
    }
  }
  .arrow {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: background .5s, opacity .5s;
    .arrow-left, .arrow-right {
      position: absolute;
      top: 50%;
      margin-top: -25px;
      display: block;
      width: 50px;
      height: 50px;
      cursor: pointer;
      opacity: 1;
      transition: background .5s, opacity .5s;
    }
    .arrow-left:hover, .arrow-right:hover {
      opacity: 1;
    }
    .arrow-left {
      left: 0;
      background: url("../img/arrow-left.png") no-repeat center #fff;
      width: 50px;
      height: 50px;
      box-shadow: 0 0 32px rgba(20, 26, 45, 0.09);
      &.arrow--small {
        background: url("../img/arrow-left-small.png") no-repeat center #fff;
        width: 30px;
        height: 30px;
        margin-top: -15px;
      }
    }
    .arrow-right {
      right: 0;
      background: url("../img/arrow-right.png") no-repeat center #fff;
      width: 50px;
      height: 50px;
      box-shadow: 0 0 32px rgba(20, 26, 45, 0.09);
      &.arrow--small {
        background: url("../img/arrow-right-small.png") no-repeat center #fff;
        width: 30px;
        height: 30px;
        margin-top: -15px;
      }      
    }
  }
}