.waves-effect {
  position: relative;
  cursor: pointer;
  display: inline-block;
  overflow: hidden;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  vertical-align: middle;
  z-index: 1;
  transition: .3s ease-out;
}

.waves-effect .waves-ripple {
  position: absolute;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  margin-top: -10px;
  margin-left: -10px;
  opacity: 0;
  background: rgba(0, 0, 0, 0.2);
  transition: all 0.7s ease-out;
  transition-property: transform, opacity;
  transform: scale(0);
  pointer-events: none;
}

.waves-effect.waves-light .waves-ripple {
  background-color: rgba(255, 255, 255, 0.45);
}

.waves-effect.waves-red .waves-ripple {
  background-color: rgba(244, 67, 54, 0.7);
}

.waves-effect.waves-yellow .waves-ripple {
  background-color: rgba(255, 235, 59, 0.7);
}

.waves-effect.waves-orange .waves-ripple {
  background-color: rgba(255, 152, 0, 0.7);
}

.waves-effect.waves-purple .waves-ripple {
  background-color: rgba(156, 39, 176, 0.7);
}

.waves-effect.waves-green .waves-ripple {
  background-color: rgba(76, 175, 80, 0.7);
}

.waves-effect.waves-teal .waves-ripple {
  background-color: rgba(0, 150, 136, 0.7);
}

.waves-effect input[type="button"], .waves-effect input[type="reset"], .waves-effect input[type="submit"] {
  border: 0;
  font-style: normal;
  font-size: inherit;
  text-transform: inherit;
  background: none;
}

.waves-effect img, .waves-effect span {
  position: relative;
  z-index: -1;
  vertical-align: middle;
}

.waves-notransition {
  transition: none !important;
}

.waves-circle {
  transform: translateZ(0);
  -webkit-mask-image: -webkit-radial-gradient(circle, white 100%, black 100%);
}

.waves-input-wrapper {
  border-radius: 0.2em;
  vertical-align: bottom;
}

.waves-input-wrapper .waves-button-input {
  position: relative;
  top: 0;
  left: 0;
  z-index: 1;
}

.waves-circle {
  text-align: center;
  width: 2.5em;
  height: 2.5em;
  line-height: 2.5em;
  border-radius: 50%;
  -webkit-mask-image: none;
}

.waves-block {
  display: block;
}

/* Firefox Bug: link not triggered */
.waves-effect .waves-ripple {
  z-index: -1;
}

.hamburger-icon-menu-wrapper {
  z-index: 10000;
  position: absolute;
  font-size: 16px;
  /* base font size for em values below */
}

/* ...... CSS for Hamburger UI DIV and links ...... */
.hamburger-ui {
  overflow: hidden;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 2;
  /* make .hamburgerui higher in z-index than .fullscreenmenu */
}

.hamburger-ui ul {
  margin: 0;
  padding: 0;
  font: bold 16px 'Bitter', sans-serif;
  /* .hamburgerui font size (Bitter is Google font) */
  list-style-type: none;
}

.hamburger-ui li {
  display: inline;
  margin: 0;
}

.hamburger-ui li a {
  float: left;
  display: block;
  position: relative;
  text-decoration: none;
  margin: 0;
  padding: 25px 30px;
  /*padding inside each link */
  color: white;
  outline: none;
  background: darkred;
  /*background of tabs (default state)*/
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.hamburger-ui li:last-of-type a {
  width: 35px;
  /* width of last LI A containing hamburger style icon */
}

.hamburger-ui li:last-of-type a:after {
  content: 'Menu';
  visibility: hidden;
}

.hamburger-ui li a:visited {
  color: white;
}

.hamburger-ui li a:hover {
  background: red;
  /*background of UI links for hover state  */
}

.nav-toggler {
  /* Main hamburger icon to toggle menu state */
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: 8px;
  /* change font size to change hamburger icon dimensions. Leave width/height below alone */
  width: 4em;
  height: 2.5em;
  padding: 0;
  text-indent: -1000px;
  border: 0.6em solid white;
  /* border color */
  border-width: 0.6em 0;
  cursor: pointer;
  -webkit-transform: translate3D(-50%, -50%, 0);
  transform: translate3D(-50%, -50%, 0);
  /* center hamburger icon inside link */
  -webkit-transition: all 0.3s ease-in;
  transition: all 0.3s ease-in;
}

.nav-toggler::before, .nav-toggler::after {
  /* inner strip inside hamburger icon */
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 0.6em;
  top: 50%;
  margin-top: -0.3em;
  left: 0;
  background: white;
  /* stripes background color. Change to match border color of parent label above */
  -webkit-transition: all 0.3s ease-in;
  transition: all 0.3s ease-in;
}

/* ...... CSS for Full Screen Menu NAV ...... */
.full-screen-menu {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: #B71010;
  /* background color of full screen menu */
  padding-top: 80px;
  z-index: 1;
  visibility: hidden;
  overflow: auto;
  font-family: 'Bitter', sans-serif;
  /* 'Bitter' is Google font */
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transform: translate3D(0, -100%, 0);
  /* hide menu initially on screen by offsetting it vertically */
  transform: translate3D(0, -100%, 0);
  -webkit-transition: -webkit-transform 0.5s, visibility 0s 0.5s;
  transition: transform 0.5s, visibility 0s 0.5s;
}

.full-screen-menu ul {
  margin: 0;
  padding: 0;
  list-style: none;
  width: 100%;
  position: relative;
  font-weight: bold;
  font-size: 2.5em;
  text-transform: uppercase;
  -webkit-transform: translate3D(0, -40px, 0);
  transform: translate3D(0, -40px, 0);
  opacity: 0;
  transition: all 0.5s 0.4s;
}

.full-screen-menu ul li {
  display: inline;
  text-align: center;
  box-sizing: border-box;
  margin-bottom: 8px;
}

.full-screen-menu ul li a {
  text-decoration: none;
  position: relative;
  color: white;
  /* link color */
  padding: 25px;
  /* general spacing between links */
  display: block;
  -webkit-transition: background 0.3s;
  transition: background 0.3s;
}

.full-screen-menu ul li a:hover {
  background: darkred;
}

.fullscreenmenu ul li a:hover:after {
  width: 100%;
}

/* .... Menu Hamburger Icon UI when shrinked CSS ........ */
.hamburger-ui.shrink li:nth-last-of-type(n+2) a {
  opacity: 0;
  pointer-events: none;
}

/* .... Menu Opened State CSS ........ */
.hamburger-icon-menu-wrapper.open .full-screen-menu {
  visibility: visible;
  -webkit-transform: translate3D(0, 0, 0);
  /* show menu by shifting it vertically to visible position within container */
  transform: translate3D(0, 0, 0);
  -webkit-transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
}

.hamburger-icon-menu-wrapper.open .nav-toggler {
  border-width: 0;
}

.hamburger-icon-menu-wrapper.open .nav-toggler::before {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  /* rotate line to create "x" */
}

.hamburger-icon-menu-wrapper.open .nav-toggler::after {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  /* rotate line to create "x" */
}

.hamburger-icon-menu-wrapper.open .hamburger-ui li:nth-last-of-type(n+2) a {
  opacity: 0;
  pointer-events: none;
}

.hamburger-icon-menu-wrapper.open .full-screen-menu ul {
  opacity: 1;
  -webkit-transform: translate3D(0, 0, 0);
  transform: translate3D(0, 0, 0);
  /* restore UL to original positon */
}

/* ....... responsive layout CSS ....... */
@media (max-width: 800px) {
  /* 1st breaking point */
  .hamburger-ui li a {
    padding: 25px;
    /*padding inside each link */
  }
  .full-screen-menu ul {
    font-size: 2em;
  }
}

@media (max-width: 600px) {
  /* 2nd breaking point */
  .hamburger-ui li a {
    padding: 20px;
    /*padding inside each link */
  }
  .full-screen-menu ul {
    font-size: 1.5em;
  }
}

@media (max-width: 480px) {
  /* 3rd breaking point */
  .full-screen-menu ul {
    font-size: 1em;
  }
}

@media (min-width: 800px) {
  /* 3rd breaking point */
  .hamburger-icon-menu-wrapper {
    display: none;
  }
}

