  list-style: none;
}

.slide .dots li.active, .slide .dots li.active-big {
  background: #fff;
  opacity: 1;
}

.slide .arrow {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: background .5s, opacity .5s;
}

.slide .arrow .arrow-left, .slide .arrow .arrow-right {
  position: absolute;
  top: 50%;
  margin-top: -25px;
  display: block;
  width: 50px;
  height: 50px;
  cursor: pointer;
  opacity: 1;
  transition: background .5s, opacity .5s;
}

.slide .arrow .arrow-left:hover, .slide .arrow .arrow-right:hover {
  opacity: 1;
}

.slide .arrow .arrow-left {
  left: 0;
  background: url("../img/arrow-left.png") no-repeat center #fff;
  width: 50px;
  height: 50px;
  box-shadow: 0 0 32px rgba(20, 26, 45, 0.09);
}

.slide .arrow .arrow-left.arrow--small {
  background: url("../img/arrow-left-small.png") no-repeat center #fff;
  width: 30px;
  height: 30px;
  margin-top: -15px;
}

.slide .arrow .arrow-right {
  right: 0;
  background: url("../img/arrow-right.png") no-repeat center #fff;
  width: 50px;
  height: 50px;
  box-shadow: 0 0 32px rgba(20, 26, 45, 0.09);
}

.slide .arrow .arrow-right.arrow--small {
  background: url("../img/arrow-right-small.png") no-repeat center #fff;
  width: 30px;
  height: 30px;
  margin-top: -15px;
}

