// Colors
$color-base: #161b6e;
$color-base-bg: #ffffff;
$color-link: #2ec1ff;
$link-menu: #607079;
$link-menu-hover: #db2527;
$collection-border-color: #dddddd;
// Type
$font-weight-base: 400;
$font-size-base: 1rem;
$line-height-base: 24/16;
$font-family-base: 'PT Sans', sans-serif;

// Grid
$grid-breakpoints: (
  xs: 0,
  sm: 600px,
  md: 800px,
  lg: 1200px
);
$container-max-widths: (
  xs: 600px,
  sm: 800px,
  md: 1000px,
  lg: 1200px
);
$grid-columns: 18;
$grid-gutter-width: 30px;

// Transitions
$transition-all: .15s ease-in-out;
$transition-color: color $transition-all, background-color $transition-all, border-color $transition-all, box-shadow $transition-all;

// Spacers
$spacer: 5px;
$spacers: (
  0: 0,
  5: ($spacer * 1),
  10: ($spacer * 2),
  15: ($spacer * 3),
  20: ($spacer * 4),
  25: ($spacer * 5),
  30: ($spacer * 6),
  35: ($spacer * 7),
  40: ($spacer * 8),
  45: ($spacer * 9),
  50: ($spacer * 10),
  55: ($spacer * 11),
  60: ($spacer * 12),
  65: ($spacer * 13),
  70: ($spacer * 14),
  75: ($spacer * 15),
  80: ($spacer * 16),
  85: ($spacer * 17),
  90: ($spacer * 18),
  95: ($spacer * 19),
  100: ($spacer * 20),
  grid-gutter: $grid-gutter-width/2,
  5-inv: ($spacer * -1),
  10-inv: ($spacer * -2),
  15-inv: ($spacer * -3),
  20-inv: ($spacer * -4),
  25-inv: ($spacer * -5),
  30-inv: ($spacer * -6),
  35-inv: ($spacer * -7),
  40-inv: ($spacer * -8),
  45-inv: ($spacer * -9),
  50-inv: ($spacer * -10),
  55-inv: ($spacer * -11),
  60-inv: ($spacer * -12),
  65-inv: ($spacer * -13),
  70-inv: ($spacer * -14),
  75-inv: ($spacer * -15),
  80-inv: ($spacer * -16),
  85-inv: ($spacer * -17),
  90-inv: ($spacer * -18),
  95-inv: ($spacer * -19),
  100-inv: ($spacer * -20),
  grid-gutter-inv: -$grid-gutter-width/2
);